body {
  font: 30px "Century Gothic", Futura, sans-serif;
}

html, body, #root, #root>div {  
  height: 100%;
  width: 100%;
  margin: 0 !important;
  overflow-x: clip;
  overflow-y: clip;
}

.bar-screen-div{
  display: flex;
  flex-direction: row;
  height:100%;
  width:100%;
  margin: 0 !important;
}
@media only screen and (max-width: 950px) {
  .bar-screen-div {
    display: flex;
    flex-direction: column;
    height:100%;
    width:100%;
    margin: 0 !important;

  }
} 

.mainbar {
  box-shadow: 0px 2px 5px lightblue;
  width: 17%;
  height: auto;

  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
@media only screen and (max-width: 950px) {
  .mainbar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    height: 100px;
    margin: 0 !important;

  }
}

.mainscreen {
  /* padding: 20px;
  padding-bottom: 30px;
  padding-right: 40px; */
  width: 83%;
  height: auto;

  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
@media only screen and (max-width: 950px) {
  .mainscreen {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    width: 100%;
    height: 85%;
    overflow-y: auto;
    overflow-x: clip;

  }
}


.mainbar-button-div{
  display:flex;
  align-items:flex-end;
  flex-direction: column;
  justify-items:space-around;
  align-content:center;
  flex-wrap:wrap;
  padding:30px;
}
@media only screen and (max-width: 950px) {
  .mainbar-button-div {
    display:flex;
    align-items:flex-start;
    flex-direction: row;
    justify-content:space-around;
    align-content:center;
    width: 100%;

  }
} 

.mainbarbutton-active {
  font-size: 22px;
  margin-top: 20px;
  margin-bottom: 10px;
  width: 100%;
  padding: 5px;
  border-radius:8px;
  border: 2px solid #4095c6;
  font-weight:700;
  color:#FFFFFF;
  background-color:#4eb5f1;
  transition: all 0.15s;

  display: flex;
  align-items: center;

  text-decoration: none;
}
.mainbarbutton-active:hover {
  background-color:#4095c6;
}
@media all and (max-width:30em){
  .mainbarbutton-active {
    display:block;
    margin:0.2em auto;
  }
}
@media only screen and (max-width: 950px) {
  .mainbarbutton-active {
    max-width: 100px;
  }
} 

.mainbarbutton-inactive {
  border: 2px solid #FFFFFF;
  background-color:#FFFFFF;
  color:#777777;
}
.mainbarbutton-inactive:hover {
  background-color:#b6d9ee;
  border: 2px solid #71bbe6;

}
@media all and (max-width:30em){
  .mainbarbutton-inactive {
    display:block;
    margin:0.2em auto;
  }
}


.maintitle {
  color: rgb(0, 72, 139);
  font-size: 30px;
  text-align:center;
  margin-bottom: 15px;

  display: flex;
  align-items: center;
  width: 100%;
  padding: 5px;

  transition: all 0.15s;

  display: flex;
  align-items: center;

  text-decoration: none;
}
@media only screen and (max-width: 950px) {
  .maintitle {
  max-width: 100px;
  }
} 

.button3 {
  width: 100%;
  font-size: 30px;
  border-radius:70px;
  border: 2px solid #4095c6;
  font-weight:700;
  color:#FFFFFF;
  background-color:#4eb5f1;
  text-align:center;
  transition: all 0.15s;
}
.button3:hover {
  background-color:#4095c6;
}
@media all and (max-width:30em){
  .button3 {
    display:block;
    margin:0.2em auto;
  }
}

.textField1 {
  width: 100%;
  font-size: 30px;
  text-align: center;

  border-radius:70px;
  outline: none;
  transition: all 0.15s;
}
.textField1:hover {
  background-color: #effafd;
}
.textField1:focus {
  background-color: #dff6ff;
}



.listbutton {
  width: 100%;
  font-size: 30px;
  padding:0.1em 1.2em;
  margin:0 0px 12px 0;
  border-radius:70px;
  border: 2px solid #4095c6;
  font-weight:700;
  color:#FFFFFF;
  background-color:#4eb5f1;
  text-align:center;
  transition: all 0.15s;
}
.listbutton:hover {
  background-color:#4095c6;
}
@media all and (max-width:30em){
  .listbutton {
    display:block;
    margin:0.2em auto;
  }
}


.align-container {
  display: flex;
  justify-content: flex-end;
  flex-direction: row;
  align-items: center;
}

.align-container-tr {
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
}



.margin-container {
  margin-left: 10px;
  margin-right: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}



.square:focus {
  outline: none;
}

.secondarytext {
  color: #777777;
  font-size: large;
  text-align: center;

}


.listEntry {
  color: #777777;
  font-size: 30px;
  text-align: center;
  border-radius:10px;
  border: solid 2px #cecece;
  padding:0.25em 0.25em;
  outline: none;
  transition: all 0.15s;
}

